import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Button from './Button'
import Bubble from './Bubble'

class Home extends Component {
    render() {
        return (
            <div className="herosection">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-12 col-md-6 herotext">
                            <Bubble name="RAJESH" />
                            <h1>Code To make Life Easier!</h1>
                            <p>We trust on innovation and that help us to succeed in future! Our team is innovative and
                            interested to learn your project to offer the best solutions from out of the box!</p>
                            <Link to="/" className=""><Button name="Hire me" /></Link>

                        </div>
                        <div className="col-12 col-md-6 bgimage">

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Home;