import React, { Component } from 'react'
import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';
//import Popper from 'popper.js';
import 'bootstrap/dist/js/bootstrap.bundle.min';

import { Link } from 'react-router-dom'



class Menu extends Component {
    
       componentDidMount() {
        $(document).ready(function(){
            $(".text").hover(function(){
            $(this).toggleClass("hidden");
            $(this).css("font-size", "1em/1");
            });
           });
      }

    render() {
        return (
            <div className="Header" id="headernav">
                <nav className="navbar navbar-default">
                    <div className="container-fluid">
                        <div className="navbar-header">
                            <Link to="#" className="navbar-brand">
                                <ul className="text hidden">
                                    <li>R</li>
                                    <li className="ghost">A</li>
                                    <li className="ghost">J</li>
                                    <li className="ghost">E</li>
                                    <li className="ghost">S</li>
                                    <li className="ghost">H</li>
                                    <li className="ghost">R</li>
                                    <li>O</li>
                                    <li>Y</li>
                                    <li className="ghost">A</li>
                                    <li className="ghost">L</li>
                                    
                                </ul>
                            </Link>
                        </div>
                        <ul className="nav navbar-nav" id="topnavbar">
                            <li><Link to="/" className="first">Home</Link></li>
                            <li><Link to="/about" className="second">About</Link></li>
                            <li><Link to="/blog" className="third">Blog</Link></li>
                            <li><Link to="/project" className="forth">Project</Link></li>
                            <li><Link to="/contact" className="fifth">Contact</Link></li>
                            <div className="underbar"></div>
                        </ul>
                    </div>
                </nav>
            </div>
        )
    }
}

export default Menu;